import {Widget} from "./Widget";

export function instance()
{
    return new TestWidget();
}

export class TestWidget extends Widget
{
    header:string = "";
    content:string = "";

    constructor()
    {
        super();
        this.header = "Hello World";
        this.content = "<img src='https://img.youtube.com/vi/ttz4Sr0tZFg/maxresdefault.jpg' alt='No Image inside the widget' />";
    }
}



